import { defineStore } from 'pinia'
import { sellerRepository } from '@/infrastructure/repositories/sellerRepository'
import { ISellerRequestDTO } from '@/infrastructure/dto/sellerModel'
import { ISeller, ISellerDTO } from '@/domain/models/sellerModel'

export const useSellerStore = defineStore('sellers', {
  state: () => ({
    sellers: [] as ISeller[]
  }),

  getters: {
    getSeller (state): ISeller[] {
      return state.sellers
    }
  },

  actions: {
    async fetchAllSeller () {
      this.sellers = await sellerRepository.getAll()
    },

    async update (seller: ISeller): Promise<boolean> {
      const updateSeller: ISellerRequestDTO = {
        id: seller.id,
        name: seller.name,
        identification: seller.identification,
        observations: seller.observations,
        status: seller.status
      }
      try {
        const response: any = await sellerRepository.createSeller()
        // Verifica si el código de estado es 200 o 204
        if (response.statusCode === 200 || response.statusCode === 204) {
          return true
        } else {
          return false
        }
      } catch (error) {
        console.error('Error en la solicitud:', error)
        return false
      }
    }
  }
})
