<template>
  <div class="home">
    <div class="searchbar">
      <v-text-field
        class="search-field"
        label="Escribe un concepto"
        tile
        filled
        clearable
        v-model="search"
        :loading="searchLoading"
        @keyup.enter="searchConcept"
      ></v-text-field>
      <v-btn
        class="search-btn"
        color="primary"
        @click="searchConcept"
        :loading="searchLoading"
        :disabled="searchLoading"
      >
        Buscar
      </v-btn>
    </div>
    <div class="scores-container">
      <v-spacer></v-spacer>
      <v-slider
        thumb-label="always"
        width="100px"
        readonly
        color="primary"
        :value="leftValue"
      >
        <template v-slot:thumb-label>
          {{ leftSelected.length }}
        </template>
      </v-slider>
      <v-spacer></v-spacer>
      <h2>Concepto: {{ concept }}</h2>
      <v-spacer></v-spacer>
      <v-slider
        thumb-label="always"
        width="100px"
        readonly
        color="primary"
        :value="rightValue"
      >
        <template v-slot:thumb-label>
          {{ rightSelected.length }}
        </template>
      </v-slider>
      <v-spacer></v-spacer>
    </div>
    <span>seleccione las imagenes que mas relacione con el concepto</span>
    <div class="sides-cont">
      <div class="left-side">
        <div class="img-gallery">
          <v-row>
            <v-col
              v-for="result in left"
              :key="result.link"
              cols="12"
              sm="6"
              md="4"
            >
              <v-card
                class="mx-auto"
                max-width="400"
                @click="selectLeft(result)"
              >
                <v-img :src="result.link" height="200px"></v-img>
                <v-card-title>{{ result.title }}</v-card-title>
                <v-card-subtitle
                  >{{ result.width }} x {{ result.height }}</v-card-subtitle
                >
              </v-card>
            </v-col>
          </v-row>
        </div>
      </div>
      <v-divider vertical></v-divider>
      <div class="right-side">
        <div class="img-gallery">
          <v-row>
            <v-col
              v-for="result in right"
              :key="result.link"
              cols="12"
              sm="6"
              md="4"
            >
              <v-card
                class="mx-auto"
                max-width="400"
                @click="selectRight(result)"
              >
                <v-img :src="result.link" height="200px"></v-img>
                <v-card-title>{{ result.title }}</v-card-title>
                <v-card-subtitle
                  >{{ result.width }} x {{ result.height }}</v-card-subtitle
                >
              </v-card>
            </v-col>
          </v-row>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'SearchImage',
  components: {
    //
  },
  data: () => ({
    //
    search: '',
    searchLoading: false,
    left: [],
    right: [],
    leftSelected: [],
    rightSelected: [],
    winCondition: 20,
    concept: ''
  }),
  methods: {
    async searchConcept () {
      this.searchLoading = true
      this.left = []
      this.right = []
      this.concept = this.search
      await fetch(
        `https://www.googleapis.com/customsearch/v1?key=${process.env.VUE_APP_API_KEY}&cx=${process.env.VUE_APP_CX}&q=${this.search}&num=10&offset=0&imgSize=medium&searchType=image&fileType=jpg`,
        {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json'
          }
        }
      )
        .then((response) => response.json())
        .then((data) => {
          data.items.forEach((item, index) => {
            if (index % 2 !== 0) {
              this.left.push({
                title: item.title,
                link: item.link,
                height: item.image.height,
                width: item.image.width
              })
            } else {
              this.right.push({
                title: item.title,
                link: item.link,
                height: item.image.height,
                width: item.image.width
              })
            }
          })
        })
      this.searchLoading = false
    },
    selectLeft (item) {
      this.leftSelected.push(item)
    },
    selectRight (item) {
      this.rightSelected.push(item)
    }
  },
  computed: {
    //
    leftValue () {
      return (this.leftSelected.length * 100) / this.winCondition
    },
    rightValue () {
      return (this.rightSelected.length * 100) / this.winCondition
    }
  },
  watch: {
    //
    leftValue () {
      if (this.leftValue >= 100) alert('Ha ganado el vendedor #1')
    },
    rightValue () {
      if (this.rightValue >= 100) alert('Ha ganado el vendedor #2')
    }
  }
}
</script>
